// src/components/dashboard/PackagesSection.jsx
"use client";

import { useSession } from "next-auth/react";
import { loadStripe } from "@stripe/stripe-js";
import PackageCard from "../PackageCard";

export default function PackagesSection() {
  const { data: session } = useSession({ required: true });
  const priceIds = {
    inscr: "price_1RMB7pFV5ZpZiouCoDbA0KnV",
    pkg1:  "price_1RMBAKFV5ZpZiouCCnrjam5N",
    pkg2:  "price_1RMBFKFV5ZpZiouCJ1vHKREU",
    pkg3:  "price_1RMBIaFV5ZpZiouC8l6QjW2N",
  };

  const buy = async (priceId) => {
    const { sessionId } = await fetch("/api/stripe/checkout", {
      method: "POST",
      headers: { "Content-Type":"application/json" },
      body: JSON.stringify({ priceId }),
    }).then(r=>r.json());
    const stripe = await loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY);
    await stripe.redirectToCheckout({ sessionId });
  };

  return (
    <div className="row g-4">
      <div className="col-md-4">
        <PackageCard
          title="Inscripci√≥n Bloom"
          description="Pago √∫nico de inscripci√≥n"
          price={1000}
          onBuy={()=>buy(priceIds.inscr)}
        />
      </div>
      {["pkg1","pkg2","pkg3"].map((k,i)=>(
        <div className="col-md-4" key={k}>
          <PackageCard
            title={`Paquete ${i+1} (agua)`}
            description={`${[1,2,3][i]} clase(s)/semana ‚Üí ${[4,8,12][i]} sesiones/mes`}
            price={[1400,2250,2500][i]}
            onBuy={()=>buy(priceIds[k])}
          />
        </div>
      ))}
    </div>
  );
}
