"use client";

import { useState, useEffect, FormEvent } from "react";
import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import Calendar from "react-calendar";
import 'react-calendar/dist/Calendar.css';
import { Container, Form, Button, Alert, Row, Col } from "react-bootstrap";
import { loadStripe } from "@stripe/stripe-js";

type Servicio = {
  slug: string;
  label: string;
  priceId: string;
};

const servicios: Servicio[] = [
  { slug: "agua", label: "Estimulaci√≥n en agua", priceId:"price_1RJd0OFV5ZpZiouCasDGf28F" },
  { slug: "piso", label: "Estimulaci√≥n en piso", priceId:"price_1RJd1jFV5ZpZiouC1xXvllVc" },
  { slug: "quiropractica", label: "Quiropr√°ctica", priceId:"price_1RJd2fFV5ZpZiouCsaJNkUTO" },
  { slug: "fisioterapia", label: "Fisioterapia", priceId:"price_1RJd3WFV5ZpZiouC9PDzHjKU" },
  { slug: "masajes", label: "Masajes", priceId:"price_1RJd4JFV5ZpZiouCPjcpX3Xn" },
  { slug: "cosmetologia", label: "Cosmetolog√≠a", priceId:"price_1RJd57FV5ZpZiouCpcrKNvJV" },
  { slug: "prevencion-lesiones", label: "Prevenci√≥n de lesiones", priceId:"price_1RJd57FV5ZpZiouCpcrKNvJV" },
  { slug: "preparacion-fisica", label: "Preparaci√≥n f√≠sica", priceId:"price_1RJd6EFV5ZpZiouCYwD4J3I8" },
  { slug: "nutricion", label: "Nutrici√≥n", priceId:"price_1RJd7qFV5ZpZiouCbj6HrFJF" },
  { slug: "medicina-rehabilitacion", label: "Medicina en rehabilitaci√≥n", priceId:"price_1RJd9HFV5ZpZiouClVlCujAm" },
];

const terapeutas = [
  "Jes√∫s Ram√≠rez",
  "Miguel Ram√≠rez",
  "Alitzel Pacheco",
  "Francia",
  "Gisela",
];

export default function ReservasForm() {
  const { data: session, status } = useSession();
  const router = useRouter();

  const [servicio, setServicio] = useState<string>("");
  const [terapeuta, setTerapeuta] = useState<string>("");
  const [fecha, setFecha] = useState<Date | null>(null);
  const [horario, setHorario] = useState<number | null>(null);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (status === "unauthenticated") router.replace("/login");
  }, [status, router]);

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    if (!servicio || !terapeuta || !fecha || horario === null) {
      setError("Completa todos los campos");
      return;
    }
    try {
      const svc = servicios.find((s) => s.slug === servicio)!;
      const body = {
        userId: session?.user?.id,
        lineItems: [{ price: svc.priceId, quantity: 1 }],
        metadata: {
          servicio,
          terapeuta,
          date: fecha.toISOString(),
          hour: `${horario}:00`,
        },
      };

      const res = await fetch("/api/stripe/checkout", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body),
      });
      const data = await res.json();
      if (!res.ok) throw new Error(data.error || "Error creando sesi√≥n de pago");

      const stripe = await loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY!);
      await stripe!.redirectToCheckout({ sessionId: data.sessionId });
    } catch (err: any) {
      console.error(err);
      setError(err.message || "Error al iniciar pago");
    }
  };

  // Horarios semana & s√°bado
  const horasSemana = Array.from({ length: 9 }, (_, i) => 10 + i); // 10‚Äì18
  const horasSabado = Array.from({ length: 6 }, (_, i) => 9 + i);   // 9‚Äì14

  if (status === "loading" || !session) {
    return <Container className="py-5">Cargando‚Ä¶</Container>;
  }

  return (
    <Container className="py-5 dashboard-container">
      <h2 className="dashboard-header">Reservar cita</h2>
      {error && <Alert variant="danger">{error}</Alert>}

      <Form onSubmit={handleSubmit}>
        {/* Servicio */}
        <Form.Group className="mb-3">
          <Form.Label>Servicio</Form.Label>
          <Form.Select
            value={servicio}
            onChange={(e) => { setServicio(e.target.value); setFecha(null); setHorario(null); }}
            required
          >
            <option value="">Selecciona un servicio</option>
            {servicios.map((s) => (
              <option key={s.slug} value={s.slug}>{s.label}</option>
            ))}
          </Form.Select>
        </Form.Group>

        {/* Terapeuta */}
        <Form.Group className="mb-3">
          <Form.Label>Terapeuta</Form.Label>
          <Form.Select
            value={terapeuta}
            onChange={(e) => { setTerapeuta(e.target.value); setFecha(null); setHorario(null); }}
            required
          >
            <option value="">Selecciona un terapeuta</option>
            {terapeutas.map((t) => (
              <option key={t} value={t}>{t}</option>
            ))}
          </Form.Select>
        </Form.Group>

        {/* Calendario */}
        <Form.Group className="mb-4">
          <Form.Label>Fecha</Form.Label>
          <Calendar
            onChange={(d) => setFecha(Array.isArray(d) ? d[0] : d)}
            value={fecha}
            minDate={new Date()}
            tileDisabled={({ date }) => date.getDay() === 0} /* domingos */
            className="mb-3"
          />
        </Form.Group>

        {/* Selecci√≥n de hora */}
        {fecha && (
          <Form.Group className="mb-4">
            <Form.Label>Hora</Form.Label>
            <Row>
              {(fecha.getDay() === 6 ? horasSabado : horasSemana).map((h) => (
                <Col xs={4} md={3} key={h}>
                  <Button
                    className={`w-100 slot-btn ${horario === h ? "selected" : ""}`}
                    variant="outline-primary"
                    onClick={() => setHorario(h)}
                    type="button"
                  >
                    {h}:00
                  </Button>
                </Col>
              ))}
            </Row>
          </Form.Group>
        )}

        <Button type="submit" className="w-100 btn-orange">
          Confirmar y pagar
        </Button>
      </Form>
    </Container>
  );
}
